.app {
    position: relative;
}

* {
    padding: 0;
    margin: 0;
}

.board {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-content: space-around;
            align-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid #48aae6;
}

.square {
    margin-top: 1px;
    margin-left: 1px;
    margin-bottom: 1px;
    margin-right: 1px;
}

.square,
.btn {
    background: #48aae6;
    text-align: center;
    color: white;
}

.btn {
    position: absolute;
    -webkit-align-content: center;
            align-content: center;
    line-height: 200%;
    font-size: 150%;
}

.btn:hover {
    cursor: pointer;
}
.addRow {
    background-color:#f3a500;
}

.addColumn {
    background-color:#f3a500;
}

.addColumn, .addRow {
    transition: background-color 0.5s;
}

.addColumn:hover, .addRow:hover {
    background-color: #fcbe38;
}

.delRow {
    top: 1px;
    left: 8px;
}

.delColumn, .delRow {
    visibility: hidden;
    background-color: #b20000;
    transition:visibility 0.3s, background-color 0.4s, -webkit-transform 0.4s;
    transition:transform 0.4s, visibility 0.3s, background-color 0.4s;
    transition:transform 0.4s, visibility 0.3s, background-color 0.4s, -webkit-transform 0.4s;
}

.delColumn:hover, .delRow:hover {
    background-color: #b15252;
}


